@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body,
* {
  font-family: "Kalam", cursive;
}

body {
	font-family: 'Roboto Mono', monospace;
	background: url('https://i.imgur.com/w7uexG8.jpg');
	background-size: cover;
	background-attachment: fixed;
	background-position: center center;
}

.app {
	background-color: transparent;
	min-height: 500px;
	width: 600px;
	margin: 40px auto;
}

.h1 {
	text-align: center;
	font-size: 40px;
	color: #fff;
	position: relative;
	text-align: center;
	font-family: 'Kaushan Script', cursive;
}

.to-doForm input {
	background: #fceabb; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#f8b500,
		#fceabb
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	border: 0;
	width: 410px;
	height: 50px;
	padding: 0 20px;
	margin: 20px;
	margin-right: 0;
	font-size: 15px;
	border-radius: 18px;
	color: #353839;
	font-family: 'Kalam', cursive;
}

.to-doForm input::-webkit-input-placeholder {
	color: #696969;
}

.to-doForm input:-ms-input-placeholder {
	color: #696969;
}

.to-doForm input::-ms-input-placeholder {
	color: #696969;
}

.to-doForm input::placeholder {
	color: #696969;
}

.to-doForm input:focus,
.to-doForm button:focus {
	outline: none;
	outline-style: none;
}

.to-doForm button {
	height: 50px;
	width: 80px;
	border: 0;
	margin: 0 20px;
	border-radius: 10px;
	background-color: orangered;
	cursor: pointer;
	font-weight: lighter;
	color: #fff;
	font-size: large;
	background: #fdc830; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#fdc830,
		#f37335
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.list {
	color: #fff;
	width: 550px;
	background-color: #353839;
	height: 50px;
	margin: 20px auto;
	border-radius: 5px;
}

.list p {
	padding: 10px;
	position: relative;
}

.list p span {
	position: absolute;
	right: 10px;
	cursor: pointer;
}

.faicons {
	margin: 0 5px;
	font-size: 15px;
}

.list p input {
	background-color: transparent;
	border: 0;
	color: orange;
	font-size: 15px;
}

.list p input:focus {
	outline: none;
}

